// WeeklyMeals.js
import React, { useRef } from "react";
import { useMealData, KakaoShareButton, WeeklyMealItem } from "./mealComponent";
import { Header, Loading } from "../component";
import { useNavigate } from "react-router-dom";
import html2canvas from "html2canvas";
import jsPDF from "jspdf";

const WeeklyMeals = () => {
  const { data, handleMealClick, scrollAreaRef } = useMealData(true);
  const navigate = useNavigate();
  const exportRef = useRef(null);

  if (!Array.isArray(data) || data.length < 1) {
    return <Loading />;
  }

  const exportAsImage = async () => {
    if (exportRef.current) {
      const canvas = await html2canvas(exportRef.current, {
        scrollX: 0,
        scrollY: -window.scrollY,
        width: document.documentElement.scrollWidth,
        height: document.documentElement.scrollHeight,
        windowWidth: document.documentElement.scrollWidth,
        windowHeight: document.documentElement.scrollHeight,
        useCORS: true,
        allowTaint: false,
      });
      const imgData = canvas.toDataURL("image/png");
      const link = document.createElement("a");
      link.href = imgData;
      link.download = "일주일 식단표.png";
      link.click();
    } else {
      console.error("exportRef.current is null");
    }
  };

  return (
    <div className="flex flex-col md:flex-row h-screen bg-gray-100">
      <div
        ref={exportRef}
        className="flex flex-col items-center justify-center w-full md:w-1/2 lg:w-2/5 bg-white px-2 pb-14 relative h-screen overflow-auto"
      >
        <Header showBackButton={true} />
        <div ref={scrollAreaRef} className="flex flex-col w-full overflow-auto">
          <h1 className="text-center text-xl font-semibold text-gray-700 mb-5 mt-20">
            일주일 식단 계획
          </h1>
          {data.map((dayData, index) => (
            <div
              key={index}
              className="w-full mb-10 flex flex-col items-center"
            >
              <div className="w-full text-left font-semibold mb-2 ml-4 flex items-center">
                <div class="w-2.5 h-2.5 bg-yellow-500 rounded-full mr-1"></div>
                {dayData.date}
              </div>
              <div className="grid grid-cols-3 gap-1.5 w-full">
                {dayData.meals.map((meal, i) => (
                  <WeeklyMealItem
                    key={i}
                    meal={meal}
                    onClick={() => handleMealClick(meal.id, meal.dataType)}
                  />
                ))}
              </div>
            </div>
          ))}
          <button
            className="w-full py-2 mt-6 mb-14 bg-amber-400 text-gray-700 font-semibold rounded-full shadow-md"
            onClick={() => navigate(`/dailyMeals/1`)}
          >
            당일 식단 보러가기
          </button>
          <div className="absolute bottom-0 flex justify-between items-center w-full px-4 bg-white py-3">
            <KakaoShareButton />
            <div className="flex items-center space-x-3">
              <button className="w-8 h-8" onClick={exportAsImage}>
                <img
                  src={`${process.env.PUBLIC_URL}/static/capture.png`}
                  alt="캡처"
                  className="w-full h-full object-contain"
                />
              </button>
              <button className="w-8 h-8">
                <img
                  src={`${process.env.PUBLIC_URL}/static/pdf.png`}
                  alt="PDF"
                  className="w-full h-full object-contain"
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WeeklyMeals;
