import { createClient } from "@supabase/supabase-js";
import { useAxiosWrapper } from "./common";

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_ANON_KEY;
const esUrl = process.env.REACT_APP_ES_SEARCH_URL;
let supabase = null;
if (supabaseUrl && supabaseKey) {
  supabase = createClient(supabaseUrl, supabaseKey);
}

const supbaseWrapper = () => {
  const { api } = useAxiosWrapper();

  const insertSurvey = async (params) => {
    if (!supabase) {
      console.log("supabase deactive");
    } else {
      const { data, error } = await supabase
        .from("survey_1")
        .insert(params)
        .select();
      if (error) {
        console.error("insertSurvey error : ", error);
      }
      return data;
    }
  };
  const updateSurvey = async (id, data) => {
    if (!supabase) {
      console.log("supabase deactive");
    } else {
      const { error } = await supabase
        .from("survey_1")
        .update(data)
        .eq("id", id);
      if (error) {
        console.error("updateSurvey error : ", error);
      }
      return error;
    }
  };
  const elasticSearch = async (params) => {
    if (!esUrl) {
      console.log("REACT_APP_ES_SEARCH_URL is none");
    } else {
      const response = await api({
        method: "POST",
        url: `${esUrl}/elastic-search`,
        data: {
          indexName: "recipes_elastics",
          ...params,
        },
        headers: {},
      });

      return response;
    }
  };

  const getRecipeDetail = async (id) => {
    const response = await api({
      method: "POST",
      url: `${esUrl}/recipe-detail`,
      data: { id: id },
    });
    return response;
  };

  const getProductDetail = async (id) => {
    const response = await fetch(`${esUrl}/product-detail`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ id }),
    });

    if (!response.ok) {
      throw new Error(`Error fetching product details: ${response.statusText}`);
    }

    const data = await response.json();
    return { data };
  };

  const getMealPlan = async (surveyId) => {
    if (!esUrl) {
      console.log("REACT_APP_ES_SEARCH_URL is none");
    } else {
      const response = await api({
        method: "POST",
        url: `${esUrl}/get-meal-plan`,
        data: {
          surveyId: surveyId,
        },
        headers: {},
      });
      return response;
    }
  };

  return {
    supabase,
    insertSurvey,
    updateSurvey,
    elasticSearch,
    getRecipeDetail,
    getMealPlan,
    getProductDetail,
  };
};

export { supbaseWrapper };
